.profile__name {
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  margin: 0;
  padding: 0;
  text-overflow: ellipsis;
  overflow: hidden;
  max-width: 450px;
}

@media screen and (max-width: 873px) {
  .profile__name {
    font-style: normal;
    font-weight: 500;
    font-size: 35px;
    line-height: 40px;
    text-align: center;
  }
}

@media screen and (max-width: 577px) {
  .profile__name {
    font-style: normal;
    font-weight: 500;
    font-size: 27px;
    line-height: 33px;
    text-align: center;
    max-width: 230px;
  }
}